import { formatTag } from './formatting';

export const getUniqueTagsFromNote = (note: string, tagsArr: string[]) => {
  if (
    note.includes('#') &&
    !note.endsWith('#') &&
    (note.endsWith(' ') || !note.charAt(note.length - 1).match(/\s|\b/))
  ) {
    return [
      ...new Set(
        note
          .toLowerCase()
          .split(/(#[a-zа-я\d-]+)/gi)
          .filter(
            (tag) => tag.startsWith('#') && tag.length > 1 && !/[0-9]/.test(tag) && !tagsArr.includes(formatTag(tag))
          )
      ),
    ];
  }
  return [];
};
