import cn from 'classnames';
import React from 'react';

import { s, types } from '.';

const Button = ({ dataTestId, text, onClick, id, disabled, variant }: types.ButtonProps) => (
  <button
    className={cn(s.btn, { [s.light as string]: variant })}
    onClick={onClick}
    id={id}
    disabled={disabled}
    data-testid={dataTestId}
  >
    {text}
  </button>
);

export default Button;
